@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

$primary: #2CA5AD;
$text: #595959;

.tour-main {
  background: linear-gradient(90deg, #2D8BD3, #40BAC7);
  width: 100%;
  height: 650px;
  .title {
    display: flex;
    justify-content: center;
    padding-top: 60px;
    color: #fff;
    font-weight: 600;
  }
  .cards {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    .card {
      background: #fff;
      box-shadow: 0px 0px 4px rgba(103, 103, 103, 0.25);
      width: 80%;
      height: 400px;
      position: absolute;
      left: 10%;
    }
  }
}

@for $i from 1 through 15 {
  .card:nth-of-type(#{$i}) {
    top: ($i * 20px);
    transform: skew(0deg, $i * 0.2deg);
    opacity: ($i * 0.2);
  }
}
